<template>
	<el-form :model="dataForm" :rules="rules">
		<el-form-item label="" prop="email">
			<el-input type="text" v-model="dataForm.email" placeholder=""></el-input>
		</el-form-item>
		<el-form-item label="" prop="password">
			<el-input type="password" v-model="dataForm.password" placeholder=""></el-input>
		</el-form-item>
		<el-button @click="login"></el-button>
    <el-button type="text" @click="forgetPassVisible = true"></el-button>
    <el-dialog title="" :visible.sync="forgetPassVisible" :append-to-body="true">
      <el-steps :active="active" finish-status="success">
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
      </el-steps>
      <el-form :model="forgetForm" :rules="forgetRules" autocomplete="off">
        <el-form-item v-if="active==0" label="" prop="email">
          <el-input style="width: 300px" type="text" v-model="forgetForm.email"></el-input>
        </el-form-item>
        <el-form-item v-if="active==1" label="" prop="code">
          <el-input style="width: 300px" type="text" v-model="forgetForm.code"></el-input>
        </el-form-item>
        <el-form-item v-if="active==2" label="" prop="password">
          <el-input style="width: 300px" type="password" v-model="forgetForm.password"></el-input>
        </el-form-item>
        <el-form-item v-if="active==2" label="" prop="repeat">
          <el-input style="width: 300px" type="password" v-model="forgetForm.repeat"></el-input>
        </el-form-item>
      </el-form>
      <el-button style="margin-top: 12px;" @click="next" v-if="active<2"></el-button>
      <el-button style="margin-top: 12px" @click="submit" v-if="active==2"></el-button>
    </el-dialog>
	</el-form>
</template>

<script>
  export default {
    name: "Login",
    data() {
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''))
        } else if (value.length < 6) {
          callback(new Error('6'))
        } else {
          if (this.forgetForm.repeat !== '') {
            this.$refs.forgetForm.validateField('repeat')
          }
          callback()
        }
      }
      var validateRepeat = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''))
        } else if (value != this.forgetForm.password) {
          callback(new Error(''))
        } else {
          callback()
        }
      }
      return {
        dataForm: {name: '', password: '', email: ''},
        rules: {
          username: [{required: true, message: '', trigger: 'blur'}],
          password: [{required: true, message: '', trigger: 'blur'}],
          email: [{required: true, message: '', trigger: 'blur'}],
        },
        forgetPassVisible: false,
        active: 0,
        forgetForm: {password: '', email: '', repeat: '', code: ''},
        forgetRules: {
          code: [{required: true, message: '', trigger: 'blur'}],
          password: [{required: true, validator: validatePass, trigger: 'blur'}],
          repeat: [{required: true, validator: validateRepeat, trigger: 'blur' }],
          email: [{required: true, message: '', trigger: 'blur'}],
        },
      }
    },
		methods: {
      next() {
        if (this.active == 0) {
          this.checkEmail()
        } else {
          this.checkCode()
        }
      },
      checkEmail() {
        var params = new URLSearchParams();
        params.append('email', this.forgetForm.email);
        this.axios.post('/user/forgetPassword', params)
          .then((res)=>{
            if (res.data.code == 0) {
              this.active++
            } else {
              let remindTitle = ''
              let remindContent = ''
              this.$alert(remindContent, remindTitle, {
                confirmButtonText: ''
              });
            }
          })
          .catch((res)=>{
            console.log(res.message);
          });
      },
      checkCode() {
        var params = new URLSearchParams();
        params.append('email', this.forgetForm.email);
        params.append('code', this.forgetForm.code);
        this.axios.post('/user/checkForgetCode', params)
          .then((res)=>{
            if (res.data.code == 0) {
              this.active++
            } else {
              let remindTitle = ''
              let remindContent = ''
              this.$alert(remindContent, remindTitle, {
                confirmButtonText: ''
              });
            }
          })
          .catch((res)=>{
            console.log(res.message);
          });
      },
      submit() {
        if (this.forgetForm.password.length < 6) {
          let remindTitle = ''
          let remindContent = '6'
          this.$alert(remindContent, remindTitle, {
            confirmButtonText: ''
          });
          return
        }
        if (this.forgetForm.password !== this.forgetForm.repeat) {
          let remindTitle = ''
          let remindContent = ''
          this.$alert(remindContent, remindTitle, {
            confirmButtonText: ''
          });
          return
        }
        let params = new URLSearchParams();
        params.append('email', this.forgetForm.email);
        params.append('newPassword', this.forgetForm.password);
        this.axios.post('/user/resetForget', params)
          .then((res)=>{
            if (res.data.code == 0) {
              this.forgetPassVisible = false;
              this.active = 0
              const h = this.$createElement;
              this.$notify({
                title: '',
                message: h('i', { style: 'color: teal'}, '')
              });
            } else {
              let remindTitle = ''
              let remindContent = ''
              this.$alert(remindContent, remindTitle, {
                confirmButtonText: ''
              });
            }
          })
          .catch((res)=>{
            console.log(res.message);
          });
      },
      login () {
        var params = new URLSearchParams();
        params.append('email', this.dataForm.email);
        params.append('password',this.dataForm.password);
				this.axios.post('/user/login', params)
          .then((res)=>{
            if (res.data.code == 0) {
              const h = this.$createElement;
              this.$notify({
                title: '',
                message: h('i', { style: 'color: teal'}, '')
              });
              sessionStorage.setItem("accountId", res.data.id)
              sessionStorage.setItem("email", this.dataForm.email)
              this.$router.push("/homepage");
            } else {
              let remindTitle = ''
              let remindContent = ''
              if (res.data.code == 2) {
                remindContent = ''
              }
              this.$alert(remindContent, remindTitle, {
                confirmButtonText: ''
              });
            }
          })
          .catch((res)=>{
            console.log(res.message);
          });
      },
    }
  }
</script>
<style scoped>
  .el-form-item {
    margin-top: 0;
  }
</style>



