<template>
	<el-form :model="dataForm" :rules="rules" autocomplete="off">
    <el-form-item label="" prop="name">
      <el-input type="text" v-model="dataForm.name"></el-input>
    </el-form-item>
    <el-form-item label="" prop="email">
      <el-input type="text" v-model="dataForm.email"></el-input>
    </el-form-item>
    <el-form-item label="" prop="password">
      <el-input type="password" v-model="dataForm.password"></el-input>
    </el-form-item>
    <el-form-item label="" prop="repeat">
      <el-input type="password" v-model="dataForm.repeat"></el-input>
    </el-form-item>
    <el-button @click="register"></el-button>
    <div class="remind"></div>
  </el-form>
</template>

<script>
  export default {
    name: "Register",
    data() {
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''))
        } else if (value.length < 6) {
          callback(new Error('6'))
        } else {
          if (this.dataForm.repeat !== '') {
            this.$refs.dataForm.validateField('repeat')
          }
          callback()
        }
      }
      var validateRepeat = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''))
        } else if (value != this.dataForm.password) {
          callback(new Error(''))
        } else {
          callback()
        }
      }
      return {
        dataForm: {name: '', password: '', email: '', repeat: ''},
        rules: {
          name: [{required: true, message: '', trigger: 'blur'}],
          password: [{required: true, validator: validatePass, trigger: 'blur'}],
          repeat: [{required: true, validator: validateRepeat, trigger: 'blur' }],
          email: [{required: true, message: '', trigger: 'blur'}],
        },
      }
    },
		methods: {
      register () {
        if (this.dataForm.password.length < 6) {
          let remindTitle = ''
          let remindContent = '6'
          this.$alert(remindContent, remindTitle, {
            confirmButtonText: ''
          });
          return
        }
        if (this.dataForm.password !== this.dataForm.repeat) {
          let remindTitle = ''
          let remindContent = ''
          this.$alert(remindContent, remindTitle, {
            confirmButtonText: ''
          });
          return
        }
        let params = new URLSearchParams();
        params.append('name', this.dataForm.name);
        params.append('email', this.dataForm.email);
        params.append('password',this.dataForm.password);
        this.axios.post('/user/register', params)
          .then((res)=>{
            var remindTitle = res.data.code == 0 ? '' : '';
            var remindContent = res.data.code == 0 ? '' : '/(o)/~~';
            if (res.data.code==0){
              this.$router.push("/");
            }
            this.$alert(remindContent, remindTitle, {
              confirmButtonText: '',
            });
          })
          .catch((res)=>{
            console.log(res.data.message);
          });
      }
    }
  }
</script>
<style scoped>
  .remind {
    display: inline-block;
    font-size: 8px;
    color: red;
    text-decoration: underline;
  }
</style>



